<template>
  <div class="shop">
    <Card>
      <div slot="title" class="shopTop">
        <div>店铺管理</div>
        <el-button :type="isEdit?'primary':'success'" size="small" @click="shopEdit">{{
          isEdit ? "编辑" : "完成"
        }}</el-button>
      </div>
      <div slot="content" class="shopMessage">
        <el-form :disabled="isEdit" ref="shopForm" :model="shopForm" label-width="80px">
          <!-- 店铺名称 -->
          <el-form-item label="店铺名称">
            <el-input v-model="shopForm.name"></el-input>
          </el-form-item>
          <!-- 店铺公告 -->
          <el-form-item label="店铺公告">
            <el-input type="textarea" v-model="shopForm.bulletin"></el-input>
          </el-form-item>
          <!--店铺头像  -->
          <el-form-item label="店铺头像">
            <el-upload
              class="avatar-uploader"
              action="http://127.0.0.1:5000/shop/upload"
              :show-file-list="false"
              :on-success="imgSuccess"
              :before-upload="beforeUpload"
            >
              <img v-if="imgUrl" :src="baseUrl + imgUrl" class="avatar" />
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-form-item>
          <!-- 店铺图片 -->
          <el-form-item label="店铺图片">
            <el-upload
              action="http://127.0.0.1:5000/shop/upload"
              list-type="picture-card"
              :file-list="picsArr"
              :on-preview="imgPreview"
              :on-remove="imgRemove"
              :on-success="imgsSuccess"
            >
              <i class="el-icon-plus"></i>
            </el-upload>
            <el-dialog :visible.sync="dialogVisible">
              <img width="100%" :src="dialogImageUrl" alt="" />
            </el-dialog>
          </el-form-item>
          <!-- 配送费 -->
          <el-form-item label="配送费">
            <el-input v-model="shopForm.deliveryPrice"></el-input>
          </el-form-item>
          <!-- 起送价格 -->
          <el-form-item label="起送价格">
            <el-input v-model="shopForm.minPrice"></el-input>
          </el-form-item>
          <!-- 配送时间 -->
          <el-form-item label="配送时间">
            <el-input v-model="shopForm.deliveryTime"></el-input>
          </el-form-item>
          <!--配送描述  -->
          <el-form-item label="配送描述">
            <el-input v-model="shopForm.description"></el-input>
          </el-form-item>
          <!--店铺评分  -->
          <el-form-item label="店铺评分">
            <el-input v-model="shopForm.score"></el-input>
          </el-form-item>
          <!--销量  -->
          <el-form-item label="销量">
            <el-input v-model="shopForm.sellCount"></el-input>
          </el-form-item>
          <!-- 活动 -->
          <el-form-item label="活动">
            <el-checkbox-group v-model="shopForm.supports">
              <el-checkbox label="在线支付满28减5" name="type"></el-checkbox>
              <el-checkbox
                label="VC无限橙果汁全场8折"
                name="type"
              ></el-checkbox>
              <el-checkbox label="单人精彩套餐" name="type"></el-checkbox>
              <el-checkbox label="特价饮品8折抢购" name="type"></el-checkbox>
              <el-checkbox label="单人特色套餐" name="type"></el-checkbox>
            </el-checkbox-group>
          </el-form-item>
          <!-- 营业时间 -->
          <el-form-item label="营业时间">
            <el-time-picker
              is-range
              value-format="HH:mm:ss"
              v-model="shopForm.date"
              range-separator="至"
              start-placeholder="开始时间"
              end-placeholder="结束时间"
              placeholder="选择时间范围"
            >
            </el-time-picker>
          </el-form-item>
        </el-form>
      </div>
    </Card>
  </div>
</template>

<script>
import Card from "@/components/Card";
import { api_getShopData, api_shopEdit } from "@/api/shop";
export default {
  components: { Card },
  data() {
    return {
      //切换编辑和完成
      isEdit: true,
      baseUrl: "http://127.0.0.1:5000/upload/shop/",
      imgUrl: "",
      dialogImageUrl: "", //店铺图片地址
      dialogVisible: false,
      shopForm: {
        supports: [],
        pics:[]
      },
      picsArr: [], // [{name: 'food.jpg', url: 'https://xxx.cdn.com/xxx.jpg'}]
    };
  },
  methods: {
    //编辑按钮
    async shopEdit() {
      //切换文字
      this.isEdit = !this.isEdit;
      //数据提交
      if (this.isEdit) {
        let res = await api_shopEdit({
          id: this.shopForm.id,
          name: this.shopForm.name,
          bulletin: this.shopForm.bulletin,
          avatar: this.imgUrl,
          deliveryPrice: this.shopForm.deliveryPrice,
          deliveryTime: this.shopForm.deliveryTime,
          description: this.shopForm.description,
          score: this.shopForm.score,
          sellCount: this.shopForm.sellCount,
          supports: JSON.stringify(this.shopForm.supports),
          date: JSON.stringify(this.shopForm.date),
          pics: JSON.stringify(this.shopForm.pics),
        });
      }
    },
    //获取数据函数
    async getShop() {
      let { data } = await api_getShopData();
      //定义一个空数组
      let newPicArr = [];
      data.pics.map((item) =>
        newPicArr.push({ name: item, url: this.baseUrl + item.name })
        
      );
      console.log(data.pics);
      //头像
      this.imgUrl = data.avatar;
      //数据赋值
      this.shopForm = data;
      //照片
      this.picsArr = newPicArr;
    },
    beforeUpload(file) {
      // 判断图片上传格式
      const isJPG = file.type === "image/jpeg";
      // 限制图片的大小
      const isLt2M = file.size / 1024 / 1024 < 2;
      if (!isJPG) {
        this.$message.error("上传头像图片只能是 JPG 格式!");
      }
      if (!isLt2M) {
        this.$message.error("上传头像图片大小不能超过 2MB!");
      }
      return isJPG && isLt2M;
    },
    //店铺图片  预览
    imgPreview(res) {
      this.dialogImageUrl = this.baseUrl + res.name;
      this.dialogVisible = true;
    },
    //多图删除时的钩子
    imgRemove(res) {
      this.picsArr.forEach((item, i) => {
        if (item.name == res.name) {
          this.picsArr.splice(i, 1);
        }
      });
      this.shopForm.pics = this.picsArr.map((item) => item.name);
    },
    // 头像成功上传
    imgSuccess(val) {
      this.imgUrl = val.imgUrl;
    },
    // 店铺图片成功上传
    imgsSuccess(res) {
      console.log(res);
      this.picsArr.push({ name: res.imgUrl, url: this.baseUrl + res.imgUrl });
      // this.shopForm.pics = this.picsArr;
    },
  },
  created() {
    this.getShop();
  },
};
</script>

<style lang="less" scoped>
.shop {
  .shopTop {
    display: flex;
    justify-content: space-between;
  }
  .shopMessage {
    height: 600px;
    overflow: scroll;

    /deep/.el-form {
      input {
        width: 50%;
      }
      .el-textarea__inner {
        width: 50%;
      }
      .el-upload {
        width: 120px;
        height: 120px;
      }
      .avatar-uploader .el-upload {
        border: 1px dashed #d9d9d9;
        border-radius: 6px;
        cursor: pointer;
        position: relative;
        overflow: hidden;
      }
      .avatar-uploader .el-upload:hover {
        border-color: #409eff;
      }
      .avatar-uploader-icon {
        font-size: 28px;
        color: #8c939d;
        height: 120px;
        width: 120px;
        line-height: 120px;
        text-align: center;
      }
      .avatar {
        width: 120px;
        height: 120px;
        display: block;
      }
    }
  }
}
</style>